<template>
    <div>
        <div class="register-box">
            <h1 class="logo"></h1>
            <h1>账号注册</h1>
            <div class="account">
                <input type="text" placeholder="账号" v-model.trim="account">
            </div>
            <div class="password">
                <span class="el-icon-view" @click="see"></span>
                <input :type="ispaw" placeholder="密码" v-model.lazy.trim="password">
            </div>
            <div class="password">
                <span class="el-icon-view" @click="see"></span>
                <input :type="ispaw" placeholder="确认密码" v-model.lazy.trim="againpassword">
            </div>
            <button class="register" @click="register">注册</button>
            <div class="read">
                <input type="checkbox" v-model="agree">
                <h6>我已阅读并同意遵守<a href="#" @click.prevent="fl">法律声明</a>和<a href="#" @click.prevent="ys">隐私条款</a></h6>
            </div>
            <div class="register-bottom">
                <h6>已有Home账号，则点此<a href="#" @click.prevent="login">登录</a></h6>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'RegisterDas',
        data() {
            return {
                ispaw:'password',//用于更改输入框的类型
                account:'',//账号
                password:'',//密码
                againpassword:'',//再一次确认的密码，判断是否前后输入一致
                agree:false,//判断是否勾选用户协议
            }
        },
        methods: {
            // 查看密码
            see(){
                if(this.ispaw =='password'){
                    this.ispaw = 'text'
                }else{
                    this.ispaw = 'password'
                }
            },
            // 注册
            register(){
                if(this.agree !== false){
                    if(this.account !== localStorage.getItem(`name:${this.account}`)){
                        if(this.password == this.againpassword){
                            localStorage.setItem(`name:${this.account}`,this.account)
                            localStorage.setItem(`${this.account}:${this.password}`,this.password)
                            this.$message.success('账号成功注册！！！');
                        }else{
                            this.$message.error({
                                showClose: true,
                                message: '前后密码输入不一致，可点击一旁密码显示按钮进行确认后再重新操作',
                            });
                        }
                    }else{
                        this.$message.error({
                            showClose: true,
                            message: '该账户已注册，请更换账号重新操作',
                        });
                    }
                }else{
                    this.$message.error({
                        showClose: true,
                        message: '请勾选法律声明和隐私条款的选项框'
                    });
                }
            },
            // 法律声明
            fl(){
                this.$message.warning({
                    showClose: true,
                    message: '此仅为个人练习模仿开源项目，仅为学习，承担不起任何法律责任',
                });
            },
            // 隐私条款
            ys(){
                this.$message.warning({
                    showClose: true,
                    message: '本网站将不会严格遵守有关法律法规和本隐私政策所载明的内容收集、使用您的信息',
                });
            },
            // 已有账号跳转登录界面
            login(){
                this.$router.replace({
                    name:'login'
                })
            }
        },
        
    }
</script>

<style scoped>
    .register-box{
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 100px auto;
        width: 400px;
        height: 500px;
        padding: 20px 50px 0 50px;
        background-color: #fff;
        border-radius: 15px;
        box-shadow: 0 9px 30px -6px rgb(0 0 0 / 20%),
        0 18px 20px -10px rgb(0 0 0 / 4%),
        0 18px 20px -10px rgb(0 0 0 / 4%),
        0 10px 20px -10px rgb(0 0 0 / 4%);
    }
    .logo{
        margin-bottom: 10px;
        width: 80px;
        height: 80px;
        background: url('../../assets/images/logo.jpg') no-repeat no-repeat center;
        background-size: 115px 115px;
    }
    h1{
        text-align: center;
        margin-bottom: 5px;
        font-size: 28px;
        color: #333;
    }
    input{
        margin-top: 10px;
        margin-bottom: 5px;
        padding-left: 10px;
        width: 300px;
        height: 40px;
        border-radius: 10px;
        border: 1px solid #00826b;
        transition: all .12s linear;
    }
    input:hover{
        width: 305px;
        height: 40px;
    }
    .password{
        position: relative;
        line-height: 40px;
    }
    .password span{
        position: absolute;
        right: 15px;
        top: 21px;
    }
    .register{
        margin-top: 10px;
        width: 300px;
        height: 40px;
        border-radius: 20px;
        border: 0;
        background-color: rgb(0, 130, 107, 0.9);
        font-size: 16px;
        color: #fff;
    }
    .read{
        display: flex;
        padding-top: 25px;
        width: 300px;
        height: 15px;
        align-items: center;
        justify-content: flex-start;
    }
    .read input{
        margin: 0 5px 0 0;
        width: 15px;
        height: 15px;
    }
    .read a{
        padding: 0 3px;
        height: 15px;
        line-height: 15px;
        color: rgb(0, 0, 255, 0.6);
        font-size: 15px;
    }
    .register-bottom{
        margin-top: 30px;
        padding-top: 15px;
        width: 300px;
        border-top: 2px solid #00826b;
        color: #00826b;
    }
    .register-bottom h6{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 300px;
    }
    .register-bottom a{
        display: block;
        height: 14px;
        line-height: 14px;
        margin-left: 5px;
        height: 14px;
        color: rgb(0, 0, 255, 0.6);
    }
</style>