                    {name:'小米小爱音箱 Play',src:require ('../../assets/images/live/home1-10.jpg'),money:399,sm:'Hi-Fi级音频芯片 旗舰级音质'},
                ],
            }
        },
        methods:{
            // 搜索区间内的产品
            search(){
                if(this.firstmoney != '' && this.secondmoney != ''){
                    let searchlist = this.list.filter((p)=>{
                        return p.money >= this.firstmoney && p.money <= this.secondmoney
                    })
                    this.list = searchlist
                }else{
                    this.$message.error('价格区间输入有误请重新输入后再搜索');
                }
                
            },
            // 清空
            clear(){
                if(this.oldlist !== ''){
                    this.list = this.oldlist
                    this.firstmoney = ''
                    this.secondmoney= ''
                }
            },
            show(l){
                this.$router.push({
                    name:'show',
                    params:{
                        name:l.name,
                    }
                })
                sessionStorage.setItem('name',l.name)
                sessionStorage.setItem('src',l.src)
                sessionStorage.setItem('money',l.money)
                sessionStorage.setItem('sm',l.sm)
            }
        },
        computed:{
            newarrlist(){
                const arrlist = this.list
                if(this.type){
                    arrlist.sort((a,b)=>{
                        return this.type ===1 ? a.money - b.money : b.money - a.money
                    })
                }
                return arrlist
            }
        },
        watch:{
            list:{
                handler(_,oldvalue){
                    this.oldlist = oldvalue
                }
            }
        }
    }
</script>

