<template>
    <div>
        <HeadDas></HeadDas>
        <NavDas></NavDas>
         <el-row>
            <el-col :span="24" class="bg-purple-dark">
                <div class="w box">
                    <h1 class="h-top">购物车清单</h1>
                    <div class="null" v-show="!shoplist.length">
                        <h2>购物车为空，请前去选购吧！！！</h2>
                        <img src="../../assets/images/null.png" alt="">
                    </div>
                    <div v-show="shoplist.length">
                        <div class="title">
                            <h2>商品信息</h2>
                            <h2>单价</h2>
                            <h2>数量</h2>
                            <h2>合计</h2>
                            <h2>操作</h2>
                        </div>
                        <div class="car" v-for="s in shoplist" :key="s.id">
                            <h3><img :src="s.src" alt=""></h3>
                            <h3>{{s.name}}</h3>
                            <h3>{{s.money}}</h3>
                            <h3><button class="jian" @click="jian(s.id)">-</button>{{s.num}}<button class="jia" @click="jia(s.id)">+</button></h3>
                            <h3 style="color:red"><strong>{{s.num*s.money}}</strong></h3>
                            <h3><button @click="cleargood(s.id)">x</button></h3>
                        </div>
                        <div class="h-bottom">
                            <h5>总计：<span>￥{{sum}}</span></h5>
                            <el-button type="primary" style="backgroundColor:#00826b" @click="carinmap">结算</el-button>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <FooterDas></FooterDas>
    </div>
       
</template>

<script>
import { mapState } from 'vuex'
import HeadDas from '@/components/head/HeadDas.vue'
import NavDas from '@/components/head/NavDas.vue'
import FooterDas from '@/components/foot/FooterDas.vue'  
    export default {
        name:'CarDas',
        components:{HeadDas,NavDas,FooterDas},
        methods: {
            // 减
            jian(id){
                for(let key in this.$store.state.shoplist){
                    if(id == this.$store.state.shoplist[key].id){
                        this.$store.dispatch('Jian',id)
                    }
                }
            },
            // 加
            jia(id){
                for(let key in this.$store.state.shoplist){
                    if(id == this.$store.state.shoplist[key].id){
                        this.$store.dispatch('Jia',id)
                    }
                }
            },
            // 操作对应的商品
            cleargood(id){
                 for(let key in this.$store.state.shoplist){
                    if(id == this.$store.state.shoplist[key].id){
                        this.$store.dispatch('Cleargood',id)
                    }
                }
            },
            // 点击结算跳转到map页面
            carinmap(){
                this.$router.replace({
                    name:'map'
                })
            }
        },
        computed:{
            shoplist(){
                return this.$store.state.shoplist
            },
            ...mapState(['id','name','money','num','src']),
            // 利用计算属性算所有商品的金额
            sum(){
                let sum = 0
                for(let key in this.$store.state.shoplist){
                    sum = sum + Number(this.$store.state.shoplist[key].money*this.$store.state.shoplist[key].num)
                }
                return sum
            }
        }
    }
</script>

<style scoped>
    .bg-purple-dark{
        padding-top: 180px;
        padding-bottom: 50px;
        background-color: #fff;
    }
    .box{
        position: relative;
        background-color: #fff;
    }
    .h-top{
        padding-left: 20px;
        height: 50px;
        line-height: 50px;
        font-size: 20px;
        font-weight: 300;
        color: #fff;
        background-color: #00826b;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .title{
        display: flex;
        align-items: center;
        justify-content: space-around;
        height: 50px;
        border-bottom: 1px solid rgba(204, 204, 204, 0.5);
    }
    .title h2:nth-child(1){
        width: 40%;
    }
    .title h2{
        width: 15%;
        text-align: center;
    }
    .car{
        display: flex;
        align-items: center;
        height: 150px;
        border-bottom: 1px solid rgba(204, 204, 204, 0.5);
    }
    img{
        width: 100px;
        height: 100px;
        border: 2px solid rgba(204, 204, 204, 0.5);
    }
    .car h3:nth-child(1),.car h3:nth-child(2){
        width: 20%;
    }
    .car h3{
        width: 15%;
        text-align: center;
    }
    .car h3:nth-child(2){
        font-size: 18px;
        font-weight: 500;
        color: #333;
    }
    .car h3:nth-child(6) button{
        width: 25px;
        height: 25px;
        border-radius: 12.5px;
        border: 0;
        font-size: 20px;
        text-align: center;
        line-height: 25px;
        color: #333;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
        background-color: #fff;
    }
    .car .jian,.jia{
        margin: 0 10px;
        width: 25px;
        height: 25px;
        border-radius: 12.5px;
        border: 0;
        font-size: 20px;
        text-align: center;
        line-height: 25px;
        color: #333;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
        background-color: #fff;
    }
    /* 底部 */
    .h-bottom{
        display: flex;
        margin-top: 30px;
        align-items: center;
        justify-content: flex-end;
    }
    .h-bottom h5 span{
        margin-right: 20px;
        font-size: 20px;
        color: crimson;
    }
    /* 当购物车为空时 */
    .null h2{
        padding: 20px 0;
        text-align: center;
        font-size: 20px;
        font-weight: 600;
        color: #00826b;
    }
    .null img{
        width: 100%;
        height: 500px;
        border: 0;
    }
</style>