// 该文件用于创建vuex最核心的store

// 引入vue
import Vue from 'vue';
// 引入vuex
import Vuex from 'vuex';

// 使用vuex插件
Vue.use(Vuex)

// 准备actions--用于响应组件中的动作
const actions = {
    Add(context,value){
        context.commit('Addcar',value)
    },
    Jian(context,value){
        context.commit('Jiangood',value)
    },
    Jia(context,value){
        context.commit('Jiagood',value)
    },
    Clear(context,value){
        context.commit('ClearArr',value)
    },
    Cleargood(context,value){
        context.commit('ClearGood',value)
    }
}

// 准备mutations--用于操作数据(state)
const mutations = {
    Addcar(state,value){
        state.shoplist.unshift(value)
    },
    Jiangood(state,value){
        for(let key in state.shoplist){
            if(value == state.shoplist[key].id){
                if(state.shoplist[key].num > 1){
                    state.shoplist[key].num = state.shoplist[key].num-1
                }else{
                    state.shoplist[key].num == 1
                }
            }
        }
    },
    Jiagood(state,value){
        for(let key in state.shoplist){
            if(value == state.shoplist[key].id){
                state.shoplist[key].num = state.shoplist[key].num+1
            }
        }
    },
    // 通过给数组设置长度为0清空数组
    ClearArr(state,value){
        state.shoplist.length = value
    },
    ClearGood(state,value){
        for(let key in state.shoplist){
            if(value == state.shoplist[key].id){
                state.shoplist = state.shoplist.filter((list)=>{
                    return list.id !== value
                })
            }
        }
    }
}

// 准备state--用于存储数据
const state = {
    shoplist:[]
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
})