<template>
    <div class="photobox">
        <div class="w box">
            <div class="right-box">
                <a href="#" @click.prevent="show"><img :src="imgArr[index]" alt="" /></a>
            </div>
            <ul class="dot">
                <li v-for="(i,index) in imgArr" :key="index" @click="into($event,index)" class="nointo"></li>
            </ul>
            <div class="piece el-icon-arrow-left" @click="piece"></div>
            <div class="next el-icon-arrow-right" @click="next"></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PhotoDas",
        data() {
            return {
                imgArr:[
                    require ('../../assets/images/rotation-chart-1.jpg'),
                    require ('../../assets/images/rotation-chart-2.jpg'),
                    require ('../../assets/images/rotation-chart-3.jpg'),
                    require ('../../assets/images/rotation-chart-4.jpg'),
                    require ('../../assets/images/rotation-chart-5.jpg'),
                    require ('../../assets/images/rotation-chart-6.jpg'),
                ],
                index:0
            }
        },
        methods: {
            // 下一张
            piece(){
                this.index++
            },

            next(){
                this.index--
            },
            into(e,index){
                this.index = index
            },
            // 定时器时间
            setTimer(){
                this.timer = setInterval(this.next,4000);
            },
            show(){
                this.$router.replace({
                    name:'show',
                })
            }
        },
        watch:{
            index(){
                if (this.index >= this.imgArr.length) {
                    this.index = 0
                }else if(this.index < 0){
                    this.index = this.imgArr.length-1
                }
            },
        },
        mounted(){
            //页面加载完成后执行方法
            clearInterval(this.timer)
            this.setTimer()
        },
        beforeDestroy() {
            //页面关闭前关闭定时器
            clearInterval(this.timer)
        },
    }
</script>

<style scoped>
    .photobox{
        margin-top: 180px;
        height: 460px;
        border-color: orange;
    }
    .box {
        position: relative;
        height: 450px;
    }
    .box img{
        width: 100%;
        height: 460px;
        border-radius: 15px;
        border: 1px solid rgba(0, 130, 107, 0.1);
    }
    .box .piece,.next{
        display: none;
        position: absolute;
        margin-top: 3px;
        top: 50%;
        transform: translateY(-25px);
        width: 50px;
        height: 50px;
        text-align: center;
        line-height: 50px;
        border-radius: 25px;
        font-size: 35px;
        background-color: rgba(204, 204, 204, 0.3);
        color: #fff;
        transition: all 2s;
    }
    .box .piece{
        left: 15px;
    }
    .box .next{
        right: 15px;
    }
    .box:hover .piece,.box:hover .next{
        display: block;
    }
    .piece:hover,.next:hover{
        background-color: rgba(51, 51, 51, 0.6);
    }
    .box .dot{
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        bottom: 5%;
        left: 50%;
        transform: translateX(-50%);
    }
    .box .dot .nointo,.blank{
        margin: 0 5px;
        width: 12px;
        height: 12px;
        border-radius: 12px;
        border: 2px solid #fff;
        background-color: #ccc;
    }
    .nointo{
        background-color: #ccc;
    }
    .box .dot .blank{
        background-color: #00826b;
    }
</style>