import Vue from 'vue'
import App from './App.vue'

// 引入vuex
import store from './store/index';

// 按需引入element-ui组件
import { Input,Row,Col,Carousel,CarouselItem,Button,Message,Notification} from 'element-ui';

// VueRouter
import VueRouter from 'vue-router'
// 引入路由器
import router from './router/index'
Vue.config.productionTip = false

// 使用element-ui组件
Vue.use(Input)
Vue.use(Row)
Vue.use(Col)
Vue.use(Carousel)
Vue.use(CarouselItem)
Vue.use(Button)
Vue.use(Message)
Vue.use(Notification)
// 应用路由
Vue.use(VueRouter)

// 全局引入css样式
import '../public/css/common.css'

new Vue({
  render: h => h(App),
  store,
  router,
  beforeCreate(){
    // 全局总线
    Vue.prototype.$bus = this
    Vue.prototype.$message = Message
    Vue.prototype.$notify = Notification
    //  手动关闭所有Message和Notification的实例
    Message.closeAll()
    Notification.closeAll()
  }
}).$mount('#app')
