<template>
    <el-row>
        <el-col :span="24" class="bg-purple-dark">
            <div class="w box">
                <a href="#" @click.prevent="yes" v-show="isyes">{{text}}</a>
                <a href="#" @click.prevent="out" v-show="isno">{{changetext}}</a>
                <span>|</span>
                <a href="#" @click.prevent="register">注册</a>
                <span>|</span>
                <a href="#" @click.prevent="car" class="shopcar">
                    <div class="el-icon-shopping-cart-2">购物车</div>
                    <span class="car-sum">{{shoplist.length}}</span>
                </a>
            </div>
        </el-col>
    </el-row>
</template>

<script>
// 引入mapState
import { mapState } from 'vuex' 
    export default {
        name:'HeadDas',
        data() {
            return {
                text:'登录',
                changetext:'退出',
                isyes:true,
                isno:false
            }
        },
        methods:{
            // 登录
            yes(){
                this.$router.push({
                    name:'login'
                })
            },
            // 退出
            out(){
                // 清除登录时向浏览器本地添加的islogin以及其他存储的属性
                sessionStorage.clear()
                
                // 因为页面不需要重新的挂载，因此可以直接取反将退出切换为登录
                this.isyes = !this.isyes
                this.isno = !this.isno
                
                // 清空原来vuex的上面的数组,通过将数组的长度设置为0实现
                this.$store.dispatch('Clear',0)
            },
            // 注册
            register(){
                this.$router.push({
                    name:'register'
                })
            },
            // 购物车
            car(){
                // 通过读取浏览器本地缓存的数据来判断用户是否登录，登录了就可以进行路由跳转，没有就跳转登录
                if(sessionStorage.getItem('islogin')){
                    this.$router.replace({
                        name:'car',
                    })
                }else{
                    this.$router.push({
                        name:'login',
                    })
                }
            }
        },
        computed:{
            ...mapState(['shoplist']),
        },
        mounted() {
            // 当页面即将挂载时将他们原来决定是否隐藏的属性值取反，实现登录变退出文字的切换
            if(sessionStorage.getItem('islogin')){
                this.isyes = !this.isyes
                this.isno = !this.isno
            }
        },
    }
</script>

<style scoped>
    .bg-purple-dark{
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        height: 40px;
        background-color: #00826b;
    }
    .box{
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: flex-end;
        background-color: #00826b;
    }
    .box a{
        display: flex;
        color: #fff;
        align-items: center;
    }
    .box span{
        margin: 0 25px;
        color: #fff;
    }
    .box .car-sum{
        margin: 0 0 0 10px;
        display: inline-block;
        width: 20px;
        height: 100%;
        background-color: rgba(51, 51, 51, 0.5);
        color: #fff;
        text-align: center;
        border-radius: 5px;
    }
</style>