<template>
    <div>
        <div class="login-box">
            <h1 class="logo"></h1>
            <h1>账号登录</h1>
            <div class="account">
                <input type="text" placeholder="账号" v-model.trim="account">
            </div>
            <div class="password">
                <span class="el-icon-view" @click="see"></span>
                <input :type="ispaw" placeholder="密码" v-model.trim="password">
            </div>
            <button class="login" @click="login">登录</button>
            <ul>
                <li class="register" @click="register">注册Home账号</li>
                <li>|</li>
                <li class="forget" @click="forget">忘记密码?</li>
            </ul>
            <div class="login-bottom">
                <h6>其他账号登录</h6>
                <a href="#" @click.prevent="into"><img src="../../assets/images/other-login.png" alt="other-login"></a>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'LoginDas',
        data() {
            return {
                ispaw:'password',//用于更改输入框的类型
                account:'',//账号
                password:'',//密码
            }
        },
        methods: {
            // 查看密码
            see(){
                if(this.ispaw =='password'){
                    this.ispaw = 'text'
                }else{
                    this.ispaw = 'password'
                }
            },
            // 登录
            login(){
                if(this.account == localStorage.getItem(`name:${this.account}`)&&this.password == localStorage.getItem(`${this.account}:${this.password}`)){
                    // 向浏览器本地缓存islogin用于后面作为判断用户是否登录的标记
                    sessionStorage.setItem('islogin',true)
                    alert('登录成功正在跳转')
                    this.$router.replace({
                        name:'home',
                    })
                }else{
                    alert('登录信息错误，请检查后重新登录')
                }
            },
            // 注册
            register(){
                this.$router.replace({
                    name:'register'
                })
            },
            // 忘记
            forget(){
                this.$notify.info({
                    title: '找回密码',
                    message: ('i', { style: 'color: teal'}, '现阶段的项目我也没有途径可以知道你的密码，请重新注册一个用吧')
                });
            },
            // 其他方式登录
            into(){
                this.$notify.info({
                    title: '功能尚未开通',
                    message: ('i', { style: 'color: teal'}, '功能尚未开通')
                });
            }
        },
    }
</script>

<style scoped>
    .login-box{
        margin: 100px auto;
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 400px;
        height: 460px;
        padding: 20px 50px 0 50px;
        background-color: #fff;
        border-radius: 15px;
        box-shadow: 0 9px 30px -6px rgb(0 0 0 / 20%),
        0 18px 20px -10px rgb(0 0 0 / 4%),
        0 18px 20px -10px rgb(0 0 0 / 4%),
        0 10px 20px -10px rgb(0 0 0 / 4%);
    }
    .logo{
        margin-bottom: 10px;
        width: 80px;
        height: 80px;
        background: url('../../assets/images/logo.jpg') no-repeat no-repeat center;
        background-size: 115px 115px;
    }
    h1{
        text-align: center;
        margin-bottom: 5px;
        font-size: 28px;
        color: #333;
    }
    input{
        margin-top: 10px;
        margin-bottom: 5px;
        padding-left: 10px;
        width: 300px;
        height: 40px;
        border-radius: 10px;
        border: 1px solid #00826b;
        transition: all .12s linear;
    }
    input:hover{
        width: 305px;
        height: 40px;
    }
    .password{
        position: relative;
        line-height: 40px;
    }
    .password span{
        position: absolute;
        right: 15px;
        top: 21px;
    }
    .login{
        margin-top: 10px;
        width: 300px;
        height: 40px;
        border-radius: 20px;
        border: 0;
        background-color: rgb(0, 130, 107, 0.9);
        font-size: 16px;
        color: #fff;
    }
    ul{
        margin: 10px;
        display: flex;
        width: 300px;
        justify-content: flex-end;
    }
    ul li{
        height: 16px;
        padding: 3px 5px;
        font-size: 16px;
    }
    .register,.forget{
        cursor:pointer; 
        color: #00826b;
        transition: all .4s linear;
    }
    .register:hover,.forget:hover{
        color: rgba(0, 130, 107,0.6);
    }
    .login-bottom{
        display: flex;
        margin-top: 30px;
        padding-top: 15px;
        align-items: center;
        width: 300px;
        border-top: 2px solid #00826b;
        color: #00826b;
    }
    .login-bottom a{
        display: block;
        margin-left: 15px;
        width: 81px;
        height: 17px;
    }
</style>