// 引入路由VueRouter
import VueRouter from 'vue-router'

// 引入组件
import HomeDas from '@/pages/home'
import  LoginDas  from '@/pages/login'
import  RegisterDas  from '@/pages/register'
import  CarDas  from '@/pages/car'
import ShowDas from '@/pages/show'
import AllDas from '@/pages/all'
import MapDas from '@/pages/map'

// 创建一个路由器
const router =  new VueRouter({
    routes:[
        {
            path:'/',
            redirect:'/home',
            meta:{title:'HOME商城首页'}
        },
        {
            
            name:'home',
            path:'/home',
            component:HomeDas,
            meta:{title:'HOME商城首页'}
        },
        {
            name:'login',
            path:'/login',
            component:LoginDas,
            meta:{title:'登录'}
        },
        {   
            name:'register',
            path:'/register',
            component:RegisterDas,
            meta:{ischeek:false,title:'注册'}
        },
        {
            name:'car',
            path:'/car',
            component:CarDas,
            meta:{title:'购物车',certification:true}
        },
        {
            name:'show',
            path:'/show',
            component:ShowDas,
            meta:{title:``,certification:true},
        },
        {
            name:'all',
            path:'/all',
            component:AllDas,
            meta:{title:`全部商品`}
        },
        {
            name:'map',
            path:'/map',
            component:MapDas,
            meta:{title:'支付'}
        }
    ]
})

// 全局前置路由守卫
router.beforeEach((to,from,next)=>{
    // 判断是通过那个途径进入支付，显示相对应的商品
    if(from.name == 'show' && to.name == 'map'){
        sessionStorage.setItem('showmap',true)
    }
    if(from.name == 'car' && to.name == 'map'){
        sessionStorage.setItem('showcar',true)
    }
    next()
})
// 全局后置路由守卫
router.afterEach((to,from)=>{
    // 
    if (to.name == 'show') {
        document.title = to.params.name
    }else{
        document.title = to.meta.title
    }
    
    if(from.name == 'map'){
        // 离开map时将向本地浏览器添加showmap删除
        sessionStorage.removeItem('showmap')
        sessionStorage.removeItem('showcar')
    }
})
// 将创建的路由器暴露出去
export default router