<template>
    <div>
        <HeadDas></HeadDas>
        <NavDas></NavDas>
            <el-row>
                <el-col :span="24" class="bg-purple-dark">
                    <div class="w box">
                        <div class="left">
                            <img class="show1" :src="src" alt="">
                            <img class="show2" :src="src" alt="">
                        </div>
                        <div class="right">
                            <h1>{{name}}</h1>
                            <h6>{{sm}}<span>{{money}}</span></h6>
                            <h3>数量<button @click="reduce">-</button>{{n}}<button @click="increase">+</button></h3>
                            <button class="btn1" @click="addcar">加入购物车</button>
                            <button class="btn2" @click="buy">立即购买</button>
                        </div>
                        
                    </div>
                </el-col>
            </el-row>
        <FooterDas></FooterDas>
    </div>
</template>

<script>
import { nanoid } from 'nanoid'
import HeadDas from '@/components/head/HeadDas.vue'
import NavDas from '@/components/head/NavDas.vue'
import FooterDas from '@/components/foot/FooterDas.vue'  
    export default {
        name:'ShowDas',
        components:{HeadDas,NavDas,FooterDas},
        data() {
            return {
                n:1,
                name:sessionStorage.getItem('name'),
                src:sessionStorage.getItem('src'),
                money:sessionStorage.getItem('money'),
                sm:sessionStorage.getItem('sm'),
            }
        },
        methods: {
            // 减一
            reduce(){
                this.n--
                if(this.n == 0){
                    this.n = 1
                }
            },
            // 加一
            increase(){
                this.n++
            },
            // 购物车
            addcar(){
                if(sessionStorage.getItem('islogin')){
                    const carobj = {id:nanoid(),name:this.name,src:this.src,money:this.money,sm:this.sm,num:this.n}
                    this.$store.dispatch('Add',carobj)
                }else{
                    this.$router.push({
                        name:'login',
                    })
                }
            },
            // 支付
            buy(){
                // 通过读取浏览器本地缓存的数据来判断用户是否登录，登录了就可以进行路由跳转，没有就跳转登录
                if(sessionStorage.getItem('islogin')){
                    this.$router.push({
                        name:'map',
                    })
                    // 将选购的数量存储到浏览器本地的num
                    sessionStorage.setItem('num',this.n)
                }else{
                    this.$router.push({
                        name:'login',
                    })
                }
            }
        },
    }
</script>

<style scoped>
    .bg-purple-dark{
        padding-top: 180px;
        padding-bottom: 50px;
        background-color: #fff;
    }
    .box{
        display: flex;
        height: 500px;
        background-color: #fff;
        border-radius: 10px;
        border: 1px solid #ccc;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)
    }
    .left{
        position: relative;
        width: 50%;
    }
    .left .show1{
        position: absolute;
        top: 30px;
        left: 50px;
        width: 100px;
        height: 100px;
        border: 3px solid #ccc;
    }
    .left .show2{
        position: absolute;
        top: 40px;
        right: 60px;
        width: 300px;
    }
    .right{
        width: 50%;
    }
    .right h1{
        margin-top: 50px;
        width: 400px;
        height: 50px;
        font-size: 30px;
        color: #333;
        line-height: 50px;
    }
    .right h6{
        position: relative;
        padding-bottom: 40px;
        width: 400px;
        height: 30px;
        line-height: 30px;
        border-bottom: 1px solid rgb(204, 204, 204 , 0.7);
    }
    .right span{
        position: absolute;
        top: 0;
        right: 0;
        font-size: 30px;
        color: crimson;
    }
    .right h3{
        width: 400px;
        padding: 30px 0;
        font-size: 16px;
        border-bottom: 1px solid rgb(204, 204, 204 , 0.7);
    }
    .right h3 button{
        width: 25px;
        height: 25px;
        border-radius: 12.5px;
        border: 0;
        font-size: 20px;
        text-align: center;
        line-height: 25px;
        color: #333;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
        background-color: #fff;
    }
    .right h3 button:nth-child(1){
        margin-left: 30px;
        margin-right: 15px;
    }
    .right h3 button:nth-child(2){
        margin-left: 15px;
    }
    .btn1,.btn2{
        margin-top: 30px;
        margin-right: 20px;
        width: 100px;
        height: 50px;
        border: 0;
        color: #fff;
        border-radius: 5px;
    }
    .btn1{
        background-color: #00826b;
    }
    .btn2{
        color: rgba(51, 51, 51, 0.7);
        background-color: #fff;
        border: 1px solid #ccc;
    }
</style>