<template>
    <div>
        <HeadDas></HeadDas>
        <NavDas></NavDas>
            <el-row>
                <el-col :span="24" class="bg-purple-dark">
                    <div class="w box">
                        <h1 class="h-top">购物清单</h1>
                        <div class="title">
                            <h2>商品信息</h2>
                            <h2>单价</h2>
                            <h2>数量</h2>
                            <h2>合计</h2>
                        </div>
                        <!-- showmap -->
                        <div v-if="showmap" class="show-map">
                            <h3><img :src="src" alt=""></h3>
                            <h3>{{name}}</h3>
                            <h3>{{money}}</h3>
                            <h3>{{num}}</h3>
                            <h3 style="color:red"><strong>{{sum}}</strong></h3>
                        </div>
                        <!-- showcar -->
                        <div v-show="showcar" class="show-car" v-for="s in shoplist" :key="s.id">
                            <h3><img :src="s.src" alt=""></h3>
                            <h3>{{s.name}}</h3>
                            <h3>{{s.money}}</h3>
                            <h3>{{s.num}}</h3>
                            <h3 style="color:red"><strong>{{s.num*s.money}}</strong></h3>
                        </div>
                        <!-- 显示信息 -->
                        <div class="information">
                            <h2><a href="#" @click.prevent="addinformation">添加地址+</a></h2>
                            <h3 v-for="list in lists" :key="list.id">
                                <input type="radio" @change="checklists(list.id)" name="dz" :checked="list.done">
                                <h4>收货人姓名：{{list.name}}</h4>
                                <h4>收货地址：{{list.address}}</h4>
                                <h4>手机号码：{{list.phone}}</h4>
                                <button @click="deletelist(list.id)">删除</button>
                            </h3>
                        </div>
                        <!-- 添加信息 -->
                        <div v-show="isshow" class="bigbox">
                            <h1>新建收货地址</h1>
                            <h2><input type="text" v-model.trim.lazy="addressname" placeholder="收货人姓名"></h2>
                            <h2><input type="text" v-model.lazy="address" placeholder="收货地址"></h2>
                            <h2><input type="text" v-model.number.lazy="phone" placeholder="手机号码"></h2>
                            <h2><button class="save" @click="save">保存</button></h2>
                        </div>
                        <div class="h-bottom">
                            <h5>总计：<span>￥{{sum}}</span></h5>
                             <el-button type="primary" style="backgroundColor:#00826b" @click="inmap">提交订单</el-button>
                        </div>
                    </div>
                </el-col>
            </el-row>
        <FooterDas></FooterDas>
    </div>
</template>

<script>
import { nanoid } from 'nanoid'
import { mapState } from 'vuex'
import HeadDas from '@/components/head/HeadDas.vue'
import NavDas from '@/components/head/NavDas.vue'
import FooterDas from '@/components/foot/FooterDas.vue'  
    export default {
        name:'MapDas',
        components:{HeadDas,NavDas,FooterDas},
        data() {
            return {
                num:sessionStorage.getItem('num'),
                name:sessionStorage.getItem('name'),
                src:sessionStorage.getItem('src'),
                money:sessionStorage.getItem('money'),
                // 判断按照那种方式来显示
                showmap:sessionStorage.getItem('showmap'),
                showcar:sessionStorage.getItem('showcar'),
                // 添加个人信息
                addressname:'',
                address:'',
                phone:'',
                lists:JSON.parse(localStorage.getItem('lists')) || [],
                // 通过点击使其布尔值改变，从而控制，添加信息框的显示
                isshow:false,
            }
        },
        methods: {
            addinformation(){
                this.isshow = !this.isshow 
                this.addressname = ''
                this.address = ''
                this.phone = ''
            },
            // 保存添加的信息
            save(){
                const listObj = {id:nanoid(),name:this.addressname,address:this.address,phone:this.phone};
                this.lists.unshift(listObj)
                this.isshow = !this.isshow
            },
            // 勾选地址
            checklists(id){
                this.lists.forEach((list)=>{
                    if(list.id === id) list.done = !list.done
                })
            },
            // 删除先对于的信息
            deletelist(id){
                this.lists = this.lists.filter((list)=>{
                    return list.id !== id
                })
            },
            // 提交订单
            inmap(){
                if(sessionStorage.getItem('showcar')){
                    this.$router.replace({
                        name:'home'
                    })
                    // 清空原来vuex的上面的数组,通过将数组的长度设置为0实现
                    this.$store.dispatch('Clear',0)
                }else{
                    this.$router.replace({
                        name:'home'
                    })
                }
                this.$notify.success({
                    title: '成功',
                    message: '我没有商品可以给你，你也仅仅是提交成功而已',
                });
            }
        },
        computed:{
            shoplist(){
                return this.$store.state.shoplist
            },
            ...mapState(['id','name','money','num','src']),
            // 利用计算属性算所有商品的金额
            sum(){
                let sum = 0
                if(sessionStorage.getItem('showmap')){
                    sum = this.num * this.money
                }else{
                    for(let key in this.$store.state.shoplist){
                        sum = sum + Number(this.$store.state.shoplist[key].money*this.$store.state.shoplist[key].num)
                    }
                }
                return sum
            }
        },
        watch:{
            lists:{
                deep:true,
                handler(value){
                    localStorage.setItem('lists',JSON.stringify(value));
                }
            }
        }
    }
</script>

<style scoped>
    .bg-purple-dark{
        padding-top: 180px;
        padding-bottom: 50px;
        background-color: #fff;
        background-color: rgba(246, 246, 246,0.1);
    }
    .box{
        position: relative;
    }
    .h-top{
        padding-left: 20px;
        height: 50px;
        line-height: 50px;
        font-size: 20px;
        font-weight: 300;
        color: #fff;
        background-color: #00826b;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    .title{
        display: flex;
        align-items: center;
        justify-content: space-around;
        height: 50px;
        border-bottom: 1px solid rgba(204, 204, 204, 0.5);
    }
    .title h2:nth-child(1){
        width: 40%;
    }
    .title h2{
        width: 20%;
        text-align: center;
    }
    .show-map,.show-car{
        display: flex;
        align-items: center;
        height: 150px;
        border-bottom: 1px solid rgba(204, 204, 204, 0.5);
    }
    img{
        width: 100px;
        height: 100px;
        border: 2px solid rgba(204, 204, 204, 0.5);
    }
    .show-map h3,.show-car h3{
        width: 20%;
        text-align: center;
    }
    .show-map h3:nth-child(2),.show-car h3:nth-child(2){
        font-size: 18px;
        font-weight: 500;
        color: #333;
    }
    .information{
        display: flex;
        flex-direction: column;
    }
    .information h2{
        padding: 5px 0 5px 10px;
    }
    .information h3{
        display: flex;
        margin: 3px 0;
        align-items: center;
        height: 50px;
        background-color: #fff;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }
    .information h3 input{
        margin-left: 20px;
    }
    .information h3 h4{
        padding-left: 10px;
        width: 20%;
    }
    .information h3 button{
        display: none;
        width: 50px;
        height: 30px;
        border: 0;
        background-color: crimson;
        color: #fff;
    }
    .information:hover{
        cursor: pointer;
    }
    .information h3:hover button{
        display: block;
    }
    /* 添加快递信息 */
    .bigbox{
        display: flex;
        position: absolute;
        top: 0;
        left: 50%;
        flex-direction: column;
        width: 500px;
        height: 350px;
        transform: translateX(-250px);
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 10px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }
    .bigbox h1{
        padding: 15px 0;
        text-align: center;
        font-size: 26px;
        color: #333;
    }
    .bigbox h2{
        display: flex;
        margin: 10px 0;
        align-items: center;
        justify-content: space-around;
    }
    .bigbox h2 input,.bigbox h2 button{
        padding-left: 10px;
        width: 400px;
        height: 50px;
        border: 0;
        border-radius: 5px;
        background-color: rgba(204, 204, 204, 0.3);
        font-size: 16px;
        color: #333;
    }
    .bigbox h2 button{
        padding: 0;
        /* cursor: not-allowed; */
        /* background-color: #f6f6f6 */
    }
    
    /* 底部 */
    .h-bottom{
        display: flex;
        margin-top: 30px;
        align-items: center;
        justify-content: flex-end;
    }
    .h-bottom h5 span{
        margin-right: 20px;
        font-size: 20px;
        color: crimson;
    }
</style>